body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  min-height: 100vh;

  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


body {
  background-color: rgba(156, 156, 255, 0.825);
}

.sudoku-cell,
.sudoku-cell-number {
  width: 30px;
  height: 30px;
  border: 1px solid black;
  text-align: center;
  line-height: 30px;
  display: inline-block;
  font-weight: bold;
  background-color: white;
}

#supp {
  width: 100%;
  margin: 10px;
}

.sudoku-cell-border-right {
  border-right: 3px solid black;
}

.sudoku-cell-border-bot {
  border-bottom: 3px solid black;
}

#sudoku-grid,
#sudoku-number {
  display: flex;
  justify-content: center;
  width: 300px;
  flex-wrap: wrap;
  margin: auto;
  margin-top: 50px;
}

#sudoku-number {
  margin-top: 50px;
}

.sudoku-cell-number:hover,
.play-cell:hover {
  cursor: pointer;
}

.dont-play-cell:hover {
  cursor: no-drop;
}

.invisible {
  visibility: hidden;
  display: none;
  height: 0;
  width: 0;
  margin: 0;
}

.gray {
  background-color: #80808045;
}

.dont-play-cell {
  font-size: larger;
  font-style: italic;
  color: rgba(0, 0, 0, 0.65);
}

#mod,
#title {
  display: flex;
  justify-content: space-evenly;
  font-size: xxx-large;
  flex-wrap: wrap;
}

#mod {
  position: relative;
  top: 200px;
}

#mod>button,
#win>div>div>button {
  margin: 10px;
  font-size: large;
  font-weight: bold;
  padding: 25px 50px;
  border-radius: 15%;
}

#previous {
  font-size: larger;
  font-weight: bold;
  position: fixed;
  left: 25px;
}

#win {
  text-align: center;
  margin-top: -100px;
  background-color: black;
}

#win>div {
  position: absolute;
  top: 25%;
  left: 45%;
}

#win>div>h1 {
  color: white;
}

#help {
  bottom: 50px;
  text-align: center;
  width: 100%;
  position: fixed;
  color: black;
  cursor: help;
  font-style: italic;
  text-decoration: underline;
}